.island-video {
    position: fixed;
    top:0;
    left:0;
    min-width:100vw;
    min-height: 100vh;
    z-index:-2;
}

.home {
    @extend %fb-c;
    width:100%;
    min-height: 100vh;
    position: relative;
    &:after {
        background: radial-gradient(100% 19930.8% at -1.67% 14.74%,
         rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0.21) 100%);
        backdrop-filter: blur(10px);
        position: absolute;
        content: "";
        width:100%;
        min-height:100%;
        z-index:-2;
    }
}

